import React, {useEffect, useRef, useState} from 'react';
import {Text} from 'react-native';
import {ActivityIndicator, Modal} from 'react-native-paper';
import {Subject} from 'rxjs';

const loadingObs = new Subject<boolean>();
let isInitLoading = false;

export function showLoading() {
  isInitLoading = true;
  loadingObs.next(true);
}

export function hideLoading() {
  isInitLoading = false;
  loadingObs.next(false);
}

const GlobalLoading: React.FC<unknown> = () => {
  const [isVisible, setIsVisible] = useState(isInitLoading);
  const amountLoading = useRef<number>(0);

  useEffect(() => {
    let timeoutId: NodeJS.Timeout;

    const listenLoading = (value: boolean) => {
      if (value) {
        amountLoading.current++;
        setIsVisible(amountLoading.current !== 0);
      } else {
        amountLoading.current--;
        amountLoading.current = Math.max(amountLoading.current, 0);
      }

      clearTimeout(timeoutId);
      timeoutId = setTimeout(() => {
        setIsVisible(amountLoading.current !== 0);
      }, 45);
    };

    const unsubscribe = loadingObs.subscribe(listenLoading);
    return () => {
      unsubscribe.unsubscribe();
    };
  }, []);

  return (
    <Modal visible={isVisible} dismissable={false}>
      <ActivityIndicator
        animating={true}
        size="large"
        style={{marginBottom: 10}}
      />
    </Modal>
  );
};

export default GlobalLoading;
