import React, {ReactNode, useEffect, useState} from 'react';
import {StatusBar, View, ViewProps} from 'react-native';
import {Appbar} from 'react-native-paper';
import {useNavigation} from '@react-navigation/native';
import {AppNativeStackNavigation} from '@/navigations';

type HeaderProps = ViewProps & {
  back?: boolean;
  leftHeader?: ReactNode;
  centerHeader?: ReactNode;
  rightHeader?: ReactNode;
  dark?: boolean;
  title?: string;
  onPressBack?: () => void;
};

const Header: React.FC<HeaderProps> = props => {
  const {style, dark, title, back, ...restProps} = props;
  const navigation = useNavigation<AppNativeStackNavigation>();
  const [isCanGoBack, setIsCanGoBack] = useState(navigation.canGoBack());

  useEffect(() => {
    const handleFocus = () => {
      setIsCanGoBack(navigation.canGoBack());
    };

    navigation.addListener('focus', handleFocus);
    return () => {
      navigation.removeListener('focus', handleFocus);
    };
  }, []);

  return (
    <>
      <StatusBar barStyle={dark ? 'dark-content' : 'light-content'} />
      <Appbar.Header>
        {isCanGoBack ? (
          <Appbar.BackAction
            onPress={() => {
              navigation.goBack();
            }}
          />
        ) : (
          <View style={{width: 64, height: 40}} />
        )}
        <Appbar.Content
          titleStyle={{textAlign: 'center'}}
          title={title || ''}
        />
        <View style={{width: 64, height: 40}} />
      </Appbar.Header>
    </>
  );
};

export default Header;
